headerup {
  height: 65px;
  width: 100%;
  background-color: #2f2235;
}

.title-header-container {
  float: left;
  height: 100%;
  width: 200px;
}

h1 {
  color: #ffffff;
  font-size: 30px;
  font-weight: 300;
  line-height: 60px;
  margin-left: 60px;
}

.title-pattrn {
  color: #fff;
}

.title-party {
  color: #34d1d1;
  margin-left: 8px;
}

.hr-header {
  display: none;
}

.name-header-container {
  float: right;
  height: 100%;
  width: 260px;
}

.accordion-title-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.accordion {
  padding: 75px;
  padding-right: 0;
  font-size: 25px;
  font-weight: 300;
  text-transform: uppercase;
}

.accordion-title-container hr {
  width: 70%;
  margin-right: 75px;
}

@media screen and (max-width: 680px) {
  .headerup {
    height: 105px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .title-header-container {
    float: none;
    width: auto;
  }

  h1 {
    font-size: 35px;
    margin: 0;
  }

  .hr-header {
    width: 115px;
    display: block;
    border: solid 0.5px #d1d2d3;
    opacity: 0.25;
  }
}

.header-bar-container {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 100%;
  margin: auto;
}


