body {
  font-family: "Roboto", sans-serif;
}
/*--------------------------------------------------------------
# Navigation Menu
--------------------------------------------------------------*/
.nav-back {
  background: #124679f0;
  color: #fff;
}
.navbar-brand {
  color: #fff;
}
.navbar-brand:hover {
  color: #7e2323;
}
.nav-link {
