/*--------------------------------------------------------------
# Hero Section
--------------------------------------------------------------*/
#hero {
  width: 100%;
  height: 80vh;
  background: url("hero-bg.jpg") center center;
  background-size: cover;
  position: relative;
  margin-top: 3.5rem;
}
#herobr {
  width: 100%;
  height: 80vh;
  background: url("https://www.drrahulagarwal.in/wp-content/uploads/2022/06/dr_profile.jpg")
    center center;
  background-size: cover;
  position: relative;
  margin-top: 3.5rem;
}

#hero .container {
  padding-top: 80px;
}

#hero:before {
  content: "";
  background: rgba(0, 0, 0, 0.6);
  position: absolute;
  bottom: 0;
  top: 0;
  left: 0;
  right: 0;
}

#hero h1 {
  margin: 0 0 10px 0;
  font-size: 48px;
  font-weight: 700;
  line-height: 56px;
  color: #fff;
}

#hero h2 {
  color: #eee;
  margin-bottom: 40px;
  font-size: 15px;
  font-weight: 500;
  font-family: "Open Sans", sans-serif;
  letter-spacing: 0.5px;
  text-transform: uppercase;
}

.main-btn {
  font-family: "Poppins", sans-serif;
  text-transform: uppercase;
  font-weight: 500;
  font-size: 14px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 8px 28px;
  border-radius: 50px;
  transition: 0.5s;
  margin: 10px;
  border: 2px solid #fff;
  color: #fff;
}

.main-btn:hover {
  background: #11dbcf;
  border: 2px solid #124679f0;
  text-decoration: none;
  color: #fff;
}

/*--------------------------------------------------------------
# Services
--------------------------------------------------------------*/
.service {
  text-align: center;
  padding: 70px 20px 80px 20px;
  transition: all ease-in-out 0.3s;
  background: #124679f0;
  box-shadow: #124679f0;
}

.service .icon {
  margin: 0 auto;
  width: 64px;
  height: 64px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: ease-in-out 0.3s;
  position: relative;
  background: #f5f5f5;
  border-radius: 50px;
}

.service .icon i {
  font-size: 32px;
  transition: 0.5s;
  line-height: 1;
  position: relative;
}

.service h4 {
  font-weight: 600;
  margin: 10px 0 15px 0;
  font-size: 22px;
}

.service h4 a {
  color: #000;
  transition: ease-in-out 0.3s;
}

.service p {
  line-height: 24px;
  font-size: 14px;
  margin-bottom: 0;
}

.services i {
  color: #47aeff;
}

/*----about -section*/
.about-section {
  margin-top: 5rem;
  margin-bottom: 5rem;
}

.about-heading {
  text-transform: uppercase;
}

.about-heading .about-heading-upper {
  display: block;
  font-size: 1rem;
  font-weight: 800;
}

.about-heading .about-heading-lower {
  display: block;
  font-size: 38px;
  font-weight: 100;
}

.bg-faded {
  background-color: rgba(17, 219, 207, 0.8);
}

.about-button .btn-color {
  background: #03a59a;
  font-weight: bold;
  color: #fff;
}
.about {
  position: relative;
}

@media (min-width: 992px) {
  .about .about-img {
    width: 75%;
    float: right;
  }
  .about .about-text {
    left: 0;
    width: 60%;
    margin-top: -14rem;
    position: absolute;
    margin-left: 35rem;
  }
  .about .about-text .about-button {
    width: 100%;
    left: 0;
    position: absolute;
    bottom: -2rem;
  }
}

@media (min-width: 1200px) {
  .about .about-text {
    width: 45%;
  }
}

/* ==================================================
  Medical Camp SEction
  ================================================== */
.camp {
  background-color: #11dbcf;
  padding-bottom: 0;
}

.camp h2 {
  color: #000;
  margin-bottom: 25px;
}

.camp-grid {
  margin-top: 65px;
}

.camp-grid .row {
  margin: 0;
}

.camp-grid .row > div {
  padding: 0;
}

.camp-grid .row > div .card img {
  width: 100%;
}

.camp-grid .row > div .card .camp-text {
  position: absolute;
  display: table-cell;
  vertical-align: middle;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  top: 0;
  left: 0;
  text-align: center;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease;
}

.camp-grid .row > div .card .camp-text > div {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}

.camp-grid .row > div .card .camp-text .card-title {
  color: #fff;
  font-size: 30px;
}

.camp-grid .row > div .card .camp-text .card-text {
  color: #fff;
  opacity: 0.75;
  padding: 0 45px;
}

.camp-grid .row > div .card:hover .camp-text {
  opacity: 1;
  visibility: visible;
}
/*--------------------------------------------------------------
# Team
--------------------------------------------------------------*/
.team {
  background: #fff;
}

.team .member {
  position: relative;
  box-shadow: 0px 2px 15px rgba(0, 0, 0, 0.1);
  padding: 30px;
  border-radius: 10px;
  text-align: center;
  margin-top: 21rem;
}

.team .member .image {
  overflow: hidden;
  width: 150px;
  border-radius: 50%;
  margin: 0 auto 20px auto;
}

.team .member .image img {
  transition: ease-in-out 0.3s;
}

.team .member:hover img {
  transform: scale(1.1);
}

.team .member h4 {
  font-weight: 700;
  margin-bottom: 5px;
  font-size: 20px;
  color: #36343a;
}

.team .member span {
  display: block;
  font-size: 15px;
  padding-bottom: 10px;
  position: relative;
  font-weight: 500;
}

.team .member span::after {
  content: "";
  position: absolute;
  display: block;
  width: 50px;
  height: 1px;
  background: #b5b3ba;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.team .member p {
  margin: 10px 0 0 0;
  font-size: 14px;
}

.team .member .social {
  margin-top: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.team .member .social a {
  transition: ease-in-out 0.3s;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50px;
  width: 32px;
  height: 32px;
  background: #124679f0;
}

.team .member .social a i {
  color: #fff;
  font-size: 16px;
  margin: 0 2px;
}

.team .member .social a:hover {
  background: #124679f0;
}

.team .member .social a + a {
  margin-left: 8px;
}
