/* Drop dwon menu start */
.header-menu ul li{
  position: relative;
}
.header-menu ul li ul{
  position: absolute;
  top: 100%;
  left: 0;
  width: 300px;
  background-color: #fff;
  border-top: 1px solid #dddddd;
  transition: .4s;
  font-size: 16px;
}
.header-menu ul li ul li{
  display: block;
  margin-left: -0;
}
.header-menu ul li ul li a{
  display: block;
  border: none;
  border-bottom: 1px solid #dddddd;
}
.header-menu ul ul li:first-child a{
  border-left: none;
}
/* hover part */
.header-menu ul li ul{
  opacity: 0;
  visibility: hidden;
  transform-origin: top center;
  transform: scaley(0);
}
.header-menu ul li:hover ul{
  opacity: 1;
  visibility: visible;
  transform: scaley(1);
}
